import React, { Component } from "react";
import Layout from "../../Layout";
import { firebaseDatabase, firebaseAuth } from "../../../initializers/firebase";

/*
 <form onSubmit={this.handleSubmit}>
    <input
        className="form-control"
        type="text"
        placeholder="Title"
        onChange={this.handleTitleInput}
    />
    <input
        type="text"
        className="form-control"
        placeholder="description"
        onChange={this.handleDescriptionInput}
    />
    <input type="submit" value="save" />
</form> 
*/

class Home extends Component {
	constructor() {
		super();
		this.state = {
			information: null,
			isLoading: false
		};
	}

	componentWillMount() {
		this.handleInformation();
		this.setState({ user: firebaseAuth().currentUser });
	}

	handleInformation() {
		this.setState({ isLoading: true });
		firebaseDatabase()
			.ref("information")
			.on("value", snapshot => {
				this.setState({
					information: snapshot.val(),
					isLoading: false
				});
			});
	}
	renderText() {
		if (this.state.isLoading) {
			return <h1>Cargando</h1>;
		} else {
			return (
				<div>
					<img
						className="img-dvicent"
						src={this.state.information.imgUrl}
						alt={this.state.information.titles}
					/>
					<h1>{this.state.information.title}</h1>
					<p>{this.state.information.description}</p>
					<ul class="social-networks spin-icon">
						<li>
							<a
								href={`https://www.linkedin.com/${
									this.state.linkedin
								}`}
								class="icon-linkedin"
							>
								LinkedIn
							</a>
						</li>
						<li>
							<a
								href={`https://twitter.com/${
									this.state.information.twitter
								}`}
								class="icon-twitter"
							>
								Twitter
							</a>
						</li>
						<li>
							<a
								href={`https://github.com/${
									this.state.information.github
								}`}
								class="icon-github"
							>
								GitHub
							</a>
						</li>
						<li>
							<a
								href={`https://instagram.com/${
									this.state.information.instagram
								}`}
								class="icon-instagram"
							>
								Instagram
							</a>
						</li>
					</ul>
				</div>
			);
		}
	}
	render() {
		return <Layout>{this.renderText()}</Layout>;
	}
}
export default Home;
