import React, { Component } from "react";
import FormUpdateUser from "./UpdateUser/FormUpdateUser";
// import UpdateUser from "./UpdateUser/";
import Layout from "../../Layout";
import { firebaseAuth, firebaseDatabase } from "../../../initializers/firebase";
import swal from "sweetalert";
import "./admin.css";
class Admin extends Component {
	constructor(props) {
		super(props);

		this.state = {
			user: null,
			newName: "",
			newUrlPhoto: "",
			message: "",
			title: "",
			description: "",
			imgUrl: "",
			information: {},

			twitter: "",
			github: "",
			linkedin: "",
			instagram: ""
		};

		this.handleNewNameInput = this.handleNewNameInput.bind(this);
		this.handleNewUrlPhotoInput = this.handleNewUrlPhotoInput.bind(this);
		this.handleTitleInput = this.handleTitleInput.bind(this);
		this.handleDescriptionInput = this.handleDescriptionInput.bind(this);
		this.handleImgUrlInput = this.handleImgUrlInput.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		//this.renderFormUpdateUser = this.renderFormUpdateUser.bind(this)
		this.updateUserProfile = this.updateUserProfile.bind(this);

		this.handleTwitterInput = this.handleTwitterInput.bind(this);
		this.handleGithubInput = this.handleGithubInput.bind(this);
		this.handleLinkedinInput = this.handleLinkedinInput.bind(this);
		this.handleInstagramInput = this.handleInstagramInput.bind(this);
	}

	componentWillMount() {
		this.setState({ user: firebaseAuth().currentUser });
		firebaseDatabase()
			.ref("information")
			.on("value", snapshot => {
				this.setState({
					information: snapshot.val()
				});
			});
	}

	updateUserProfile(e) {
		e.preventDefault();
		let { newName, newUrlPhoto } = this.state;
		firebaseAuth()
			.currentUser.updateProfile({
				displayName: newName,
				photoURL: newUrlPhoto
			})
			.then(() => {
				this.setState({
					message: "perfil de usuario actualizado satifactoriamente",
					newUrlPhoto: "",
					newName: ""
				});
			})
			.catch(e => console.log(e));
	}

	handleNewUrlPhotoInput(e) {
		this.setState({ newUrlPhoto: e.target.value });
	}
	handleNewNameInput(e) {
		this.setState({ newName: e.target.value });
	}

	handleTitleInput(e) {
		this.setState({ title: e.target.value });
	}

	handleDescriptionInput(e) {
		this.setState({ description: e.target.value });
	}
	handleImgUrlInput(e) {
		this.setState({ imgUrl: e.target.value });
	}

	handleInstagramInput(e) {
		this.setState({ instagram: e.target.value });
	}
	handleTwitterInput(e) {
		this.setState({ twitter: e.target.value });
	}
	handleGithubInput(e) {
		this.setState({ github: e.target.value });
	}
	handleLinkedinInput(e) {
		this.setState({ linkedin: e.target.value });
	}

	handleSubmit(e) {
		e.preventDefault();
		let {
			title,
			description,
			imgUrl,
			twitter,
			github,
			linkedin,
			instagram
		} = this.state;
		swal({
			title: "Estas seguro que deseas actualizar?",
			// text:
			// 	"Once deleted, you will not be able to recover this imaginary file!",
			icon: "warning",
			buttons: "Actualizar datos",
			dangerMode: true
		}).then(actualizado => {
			if (actualizado) {
				firebaseDatabase()
					.ref("information")
					.set({
						title,
						description,
						imgUrl,
						twitter,
						github,
						instagram,
						linkedin
					});
				swal("Se ha actualizado tus datos correctamente", {
					icon: "success"
				});
			} else {
				swal("no se ha actualizado la informacion.");
			}
		});
	}
	render() {
		if (this.state.user.displayName === null) {
			return (
				<FormUpdateUser
					handleNewUrlPhotoInput={this.handleNewUrlPhotoInput}
					handleNewNameInput={this.handleNewNameInput}
					updateUserProfile={this.updateUserProfile}
				/>
			);
		} else {
			return (
				<Layout User={this.state.user}>
					<h2>Bienvenido {this.state.user.displayName}</h2>
					<img
						className="img-dvicent"
						src={
							this.state.information.imgUrl
								? this.state.information.imgUrl
								: this.state.imgUrl
						}
						alt="coloca la imagen"
					/>
					<form onSubmit={this.handleSubmit}>
						<div className="row">
							<div className="col-md-6">
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="Nombre"
										onChange={this.handleTitleInput}
									/>
								</div>
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="url de tu imagen"
										onChange={this.handleImgUrlInput}
									/>
								</div>
								<div className="form-group">
									<textarea
										type="text"
										className="form-control"
										placeholder="Tu descripcion"
										onChange={this.handleDescriptionInput}
									/>
								</div>
							</div>
							<div className="col-md-6">
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="Username Instagram"
										onChange={this.handleInstagramInput}
									/>
								</div>
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="Username Twitter"
										onChange={this.handleTwitterInput}
									/>
								</div>
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="Username Linkedin"
										onChange={this.handleLinkedinInput}
									/>
								</div>
								<div className="form-group">
									<input
										className="form-control"
										type="text"
										placeholder="Username Github"
										onChange={this.handleGithubInput}
									/>
								</div>
							</div>
						</div>

						<input
							type="submit"
							className="btn btn-dvicent"
							value="save"
						/>
					</form>
				</Layout>
			);
		}
	}
}

export default Admin;
